<template>
  <div class="slide-show">
		<div class="jd_banner">
			<ul class="banner_images clearfix" ref="banner_images">
				<li><a href="#"><img src="./img/l8.jpg" alt=""></a></li>
				<li><a href="#"><img src="./img/l1.jpg" alt=""></a></li>
				<li><a href="#"><img src="./img/l2.jpg" alt=""></a></li>
				<li><a href="#"><img src="./img/l3.jpg" alt=""></a></li>
				<li><a href="#"><img src="./img/l4.jpg" alt=""></a></li>
				<li><a href="#"><img src="./img/l5.jpg" alt=""></a></li>
				<li><a href="#"><img src="./img/l6.jpg" alt=""></a></li>
				<li><a href="#"><img src="./img/l7.jpg" alt=""></a></li>
				<li><a href="#"><img src="./img/l8.jpg" alt=""></a></li>
				<li><a href="#"><img src="./img/l1.jpg" alt=""></a></li>
      </ul>
		</div>
  </div>
</template>

<script>
export default {
  methods: {
    banner () {
      	//1 
	// 
	var width = document.body.offsetWidth;
	// console.log(width);\

	//   ul
	var moveUl = this.$refs['banner_images']

	// li
	var index = 1;


	//  , 
	var startTransition = function () {
		moveUl.style.transition = 'all 1s';
	}

	var endTransition = function () {
		moveUl.style.transition = '';
	}

	//    
	var setTransform = function (distance) {
		moveUl.style.transform = 'translateX('+distance+'px)';
	}


	// 
	var timeId = setInterval(function () {
		// 
		index++;

		//       
		// moveUl.style.transition = 'all .3s';
		startTransition();

		//  ul
		// moveUl.style.transform = 'translateX('+index*width*-1+'px)';
		setTransform(index*width*-1);

	},3000);


	//     index 
	moveUl.addEventListener('webkitTransitionEnd',function () {

		//   index 
		if (index>8) {
			index = 1;

			// 
			// moveUl.style.transition = '';
			endTransition();

			//   ul 
			// moveUl.style.transform = 'translateX('+index*width*-1+'px)';
			setTransform(index*width*-1);
		}else if(index<1){
			// 
			index= 8;

			// 
			// moveUl.style.transition = '';
			endTransition();

			//   ul 
			// moveUl.style.transform = 'translateX('+index*width*-1+'px)';
			setTransform(index*width*-1);
		}

		//  li class

		//  1 

	})

	var startX = 0;

	var moveX = 0;

	var distanceX = 0;

	moveUl.addEventListener('touchstart',function (event) {

		clearInterval(timeId);

		endTransition();

		startX = event.touches[0].clientX;

	})

	moveUl.addEventListener('touchmove',function (event) {
		moveX = event.touches[0].clientX - startX;
		setTransform(moveX+index*-1*width);
	})

	moveUl.addEventListener('touchend',function (event) {

		var maxDistance = width/3;

		if (Math.abs(moveX)>maxDistance) {
			if (moveX>0) {
				index--;
			}else{
				index++;
			}

			startTransition();

			setTransform(index*-1*width);

		}else{

			startTransition();

			setTransform(index*-1*width);
		}

		timeId = setInterval(function () {
			index++;
			startTransition();

			setTransform(index*width*-1);
		},1000)
	})
    }
  },
  mounted () {
    this.banner();
  }
}
//   js dom 
</script>

<style scoped>
.jd_banner{
	width: 100%;
	/* 100px */
	overflow: hidden;
	/*      positition */
	position: relative;
}

.jd_banner .banner_images{
	/* 10   10 */
	width: 1000%;
	/* 1000px */

	/*     */
	transform: translateX(-10%);
}

.jd_banner .banner_images li{
	float: left;
	width: 10%;
	/* 100px */
}

.jd_banner .banner_images li a{
	display: block;
	width: 100%;
}

.jd_banner .banner_images li a img{
	display: block;
	width: 100%;
}

/*   */
.jd_banner .banner_index{
	position: absolute;
	bottom: 10px;
	left: 50%;
	margin-left: -44px;
}

.jd_banner .banner_index li{
	float: left;
	width: 6px;
	height: 6px;
	border-radius: 50%;
	border: 1px solid white;
	margin-left: 5px;
}
/*  li .current class  
	 .current  
 */
.jd_banner .banner_index li.current{
	background-color: black;
}


/*  */
.jd_nav{
	width: 100%;
	border-bottom: 1px solid #ccc;
	background-color: white;
}
.jd_nav ul{
	width: 100%;
	/*   */
	padding-top: 5px;
}
.jd_nav ul li{
	float: left;
	width: 25%;
}
.jd_nav ul li a{
	width: 100%;
	display: block;
}
.jd_nav ul li a img{
	width: 60%;
	display: block;
	margin: 0 auto;
}
.jd_nav ul li a p{
	color: gray;
	text-align: center;
	padding:5px 0 ;
}

</style>



